import { Component, h, Element, Prop } from '@stencil/core';

@Component({
  tag: 'ks-order-summary-payment',
})
export class OrderPayU {

    @Element() root: HTMLElement;


    unique: string = "";
    componentWillLoad() {
        this.unique = Math.random().toString(36).replace('0.', '');
    }

    componentDidLoad(){
        this.root.style.display = "block"
    }

    @Prop() text: string = "";
    @Prop() image: string = "";

    @Prop() color: string = "";
    @Prop() hover: string = "";
    @Prop() active: string = "";

    render() {
        

        return(
            <div class="uk-flex uk-flex-center">
                <style innerHTML={`
                    .ks-order-summary-payment-${this.unique} {
                        background-color: ${this.color};
                        color: white;
                    }
                    .ks-order-summary-payment-${this.unique}:hover {
                        background-color: ${this.hover};
                    }
                    .ks-order-summary-payment-${this.unique}:active {
                        background-color: ${this.active};
                    }
                `}></style>

                <button type="submit" class={`uk-button uk-button-pill uk-flex uk-flex-middle uk-flex-center ks-text-decorated ks-order-summary-payment-${this.unique}`}
                        style={{fontSize: "20px", padding: "5px 20px 5px 20px"}}>
                    <div>{this.text}</div>
                    <ks-image src={this.image} width="150" height="50" class="uk-visible@s"></ks-image>
                </button>
            </div>
        );
    }
}